import { Component, OnInit, OnDestroy } from '@angular/core';
import { CartService } from './../cart.service';
import { Subscription } from 'rxjs';

@Component({
  selector: 'app-cart',
  templateUrl: './cart.component.html',
  styleUrls: ['./cart.component.scss'],
})
export class CartComponent implements OnInit, OnDestroy {
  totalPrice: number;
  orders: {
    name: string;
    size: string;
    quantity: number;
    price: number;
  }[] = [];
  private subscribale: Subscription;
  private totalSubscribe: Subscription;
  constructor(private cart: CartService) {}

  ngOnInit(): void {
    this.totalPrice = this.cart.onTotalPrice();
    this.orders = this.cart.onGetOrder();
    this.subscribale = this.cart.orderUpdated.subscribe((order) => {
      this.orders = order;
    });
    this.totalSubscribe = this.cart.orderTotal.subscribe((total) => {
      this.totalPrice = total;
    });
  }

  removeItem(item) {
    this.cart.onRemoveItem(item);
  }

  onCheckout() {
    alert(
      `The Program ends here, hope you enjoyed your meal!\nYour total expenditure was ₹ ${this.totalPrice}/-`
    );
  }

  ngOnDestroy() {
    this.subscribale.unsubscribe();
    this.totalSubscribe.unsubscribe();
  }
}
