import { Injectable } from '@angular/core';
import { Subject } from 'rxjs';

@Injectable({
  providedIn: 'root',
})
export class CartService {
  order: { name: string; size: string; quantity: number; price: number }[] = [];
  orderUpdated = new Subject<
    { name: string; size: string; quantity: number; price: number }[]
  >();
  orderTotal = new Subject<number>();
  total: number = 0;
  constructor() {}
  onAddOrder(newOrder: {
    name: string;
    size: string;
    quantity: number;
    price: number;
  }) {
    let flag = false;
    for (const elmnt of this.order) {
      if (elmnt.name === newOrder.name && elmnt.size === newOrder.size) {
        elmnt.price = newOrder.price;
        elmnt.quantity = newOrder.quantity;
        flag = true;
        break;
      }
    }
    if (!flag) {
      this.order.push(newOrder);
    }
    this.orderUpdated.next(this.order);
  }

  onGetOrder() {
    return this.order;
  }

  onRemoveItem(item: {
    name: string;
    size: string;
    quantity: number;
    price: number;
  }) {
    this.order = this.order.filter((elmnt) => elmnt !== item);
    this.orderUpdated.next(this.order);
    this.total = this.onTotalPrice();
  }

  onResetOrder() {
    this.order = [];
    this.orderUpdated.next(this.order);
    this.total = this.onTotalPrice();
  }

  onTotalPrice(): number {
    this.total = 0;
    for (const elmnt of this.order) {
      this.total += elmnt.price;
    }
    this.orderTotal.next(this.total);
    return this.total;
  }
}
