import { Component, OnInit, Input } from '@angular/core';
import { FoodItem } from './../food-item.model';
import { CartService } from './../cart.service';

@Component({
  selector: 'app-food-item',
  templateUrl: './food-item.component.html',
  styleUrls: ['./food-item.component.scss'],
})
export class FoodItemComponent implements OnInit {
  @Input('foodItem') foodItem: FoodItem;
  size: string;
  quantity: number;
  price: number = null;
  constructor(private cart: CartService) {}

  ngOnInit(): void {
    this.quantity = 0;
  }

  onSizeSelect(str) {
    this.size = str.srcElement.value;
    this.price = +this.foodItem[this.size];
  }
  onAdd() {
    this.quantity++;
    this.calculatePrice();
  }
  onReduce() {
    this.quantity--;
    this.quantity = this.quantity < 0 ? 0 : this.quantity;
    this.calculatePrice();
  }
  calculatePrice() {
    this.price = this.foodItem[this.size] * this.quantity;
  }
  onAddOrder() {
    this.cart.onAddOrder({
      name: this.foodItem.name,
      size: this.size,
      quantity: this.quantity,
      price: this.price,
    });
  }
}
